.taskItem {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    width: 240px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.5);
    height: 30px;
    padding: 5px 0;
}

.taskInfo {
    display: flex;
    gap: 10px;
}